.header-checkout {
	display: flex;
	align-items: center;
	position: relative;
	padding-bottom: 5px;
	&-wrap {
		border-bottom: 1px solid $color-dark-bg;
	}
	&__service {
		font-family: $font-firaM;
		font-size: 14px;
		font-weight: 500;
		line-height: 1.71;
		color: $color-main2;
		margin-left: 73px;
		margin-right: 71px;
	}
	&__tel {
		display: block;
		font-family: $font-fira;
		font-size: 15px;
		line-height: 1.6;
		color: $color-main2;
		cursor: pointer;
	}
	&__time {
		font-family: $font-firaM;
		font-size: 14px;
		font-weight: 500;
		line-height: 1.71;
		color: $color-main2;
	}
	&__txt {
		display: block;
		font-family: $font-fira;
		font-size: 15px;
		line-height: 1.6;
		color: $color-main2;
	}
}
.copy__block > span {
	margin-left: 18px;
}

.checkout {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	&__left {
		width: 575px;
		border-radius: 4px;
		background-color: $color-white;
		padding: 0 0 11px 0;
		border: 1px solid $color-dark-bg;
		position: relative;
	}
	&__desc-wrap {
		position: relative;
		opacity: 0;
		visibility: hidden;
		z-index: -1;
		height: 0;
		&--active {
			height: auto;
			z-index: 1;
			visibility: visible;
			opacity: 1;
		}
	}
	&__desc-top {
		border-bottom: 1px solid $color-dark-bg;
	}
	&__tab:first-child {
		padding-left: 25px;
	}
	&__tab {
		display: inline-block;
		font-size: 16px;
		color: #5f5f67;
		padding: 13px 13px 0;
		transition: color .3s;
		pointer-events: none;
		&-span {
			display: block;
			padding-bottom: 8px;
		}
		&--ok {
			color: $color-blue5;
			pointer-events: auto;
		}
		&--active {
			color: $color-main2 !important;
			pointer-events: auto;
			.checkout__tab-span {
				border-bottom: 3px solid $color-blue5;
			}
		}
	}
	&__desc-bottom {
		padding: 34px 25px 39px 31px;
	}
	&__label {
		display: flex;
		flex-direction: column;
		position: relative;
		margin-bottom: 26px;
		max-width: 200px;
		&--mobile {
			margin-bottom: 17px;
		}
		span {
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			margin-bottom: 8px;
		}
		input {
			border-radius: 3px;
			max-width: 180px;
			background-color: #ffffff;
			text-decoration: none;
			border: 1px solid rgba($color-main2, 0.3);
			padding: 8px 16px 8px;
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			transition: border-color .3s, box-shadow .3s;
			outline: none;
			&:focus {
				border-color: $color-blue5e;
				box-shadow: 0px 0px 0px 4px rgba($color-blue5e, 0.3);
			}
		}
	}
	&__txt {
		display: block;
		font-family: $font-fira;
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		margin-bottom: 8px;
	}
	&__italic {
		margin-top: 4.5px;
		font-family: $font-firaI;
		font-size: 14px;
		font-style: italic;
		line-height: 1.43;
		color: #626260;
	}
	&__box {
		display: flex;
		align-items: center;
		padding-top: 12px;
		margin-bottom: 21px;
	}
	.btn-reds {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 182px;
		height: 50px;
	}
	&__error {
		font-size: 15px;
		margin-left: 15px;
		padding-bottom: 2px;
	}
	&__right {
		width: 472px;
		border-radius: 4px;
		background-color: $color-white;
		border: 1px solid $color-dark-bg;
		position: relative;
	}
	&__top {
		padding: 13px 18px 15px;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid $color-dark-bg;
	}
	&__main {
		padding: 0 18px;
	}
	&__item {
		display: flex;
		padding: 15px 0 14px;
		border-top: 1px solid #e1e1e2;
	}
	&__item:first-child {
		border-top: none;
	}
	&__bottom {
		padding: 15px 18px 23px;
		border-top: 1px solid $color-dark-bg;
	}
	&__block {
		display: flex;
		justify-content: space-between;
		font-size: 15px;
		line-height: 1.33;
		margin-bottom: 9px;
	}
	&__block:last-child {
		font-family: $font-firaM;
		font-size: 15px;
		font-weight: 500;
		line-height: 1.33;
	}
	&__link {
		font-size: 15px;
		color: #5f5f67;
		span {
			border-bottom: 1px solid rgba(#5f5f67, 0.3);
			transition: border-bottom-color;
		}
		&:hover span{
			border-bottom-color: rgba(#5f5f67, 0.6);
		}
	}
	&__desc {
		width: 236px;
		padding-right: 10px;
	}
	&__name {
		font-size: 15px;
		line-height: 1.55;
		margin-bottom: 4px;
	}
	&__coll {
		width: 55px;
		line-height: 1.6;
		font-size: 15px;
	}
	&__sale {
		width: 53px;
		line-height: 1.6;
		font-size: 15px;
	}
	&__art {
		font-size: 14px;
		line-height: 1.43;
		color: rgba(#5f5f67, 0.8);
		margin-bottom: 4px;
	}
	&__size {
		font-family: $font-firaI;
		font-size: 14px;
		font-style: italic;
		line-height: 1.43;
	}
	&__prices {
		width: 90px;
		text-align: right;
		font-size: 15px;
		line-height: 1.6;
	}
	&__no-price {
		text-decoration: line-through;
	}
}
// delivery
.checkout {
	&__search {
		position: absolute;
		top: 45px;
		left: 16px;
		svg {
			width: 16px;
			height: 16px;
		}
	}
	&__city-link {
		display: inline-block;
		position: relative;
		border-radius: 3px;
		box-shadow: 0 1px 3px 0 rgba(182, 188, 198, 0.65);
		background-image: linear-gradient(to bottom, #ffffff, #f2f2f3);
		border: 1px solid rgba($color-main2, 0.3);
		padding: 10px 33px 10px 16px;
		cursor: pointer;
		outline: none;
		min-width: 258px;
		margin-bottom: 31px;
		&:hover {
			outline: none;
		}
		&:focus {
			outline: none;
		}
		&::before {
			content: '';
			position: absolute;
			top: 17px;
			right: 12px;
			width: 10px;
			height: 5.6px;
			background: url("../images/sprites/svg/arrow.svg") center no-repeat;
			-webkit-background-size: cover;
			background-size: cover;
		}
		.link-modal {
			min-width: 258px;
			left: -1px;
			top: 48px;
		}
	}
	&__tabs-delivery {
		display: flex;
		margin-bottom: 31px;
		position: relative;
	}
	.about__tab {
		width: 151px;
		justify-content: start;
		padding-left: 16px;
		cursor: pointer;
		height: 49px;
		span {
			margin-left: 10px;
		}
		&:active {
			.svg-shop_2 use{
				fill: $color-white;
				stroke: $color-white;
			}
			.svg-curier use{
				fill: $color-white;
			}
		}
		.svg-shop_2 use{
			fill: $color-main2;
			stroke: $color-main2;
		}
		.svg-curier use{
			fill: $color-main2;
		}
		&--active {
			.svg-shop_2 use{
				fill: $color-white;
				stroke: $color-white;
			}
			.svg-curier use{
				fill: $color-white;
			}
		}
	}
	.modal__address-link {
		display: inline-block;
	}
	.modal__min {
		margin-top: 8px;
		margin-bottom: 20px;
	}
	&__textarea {
		margin-top: 29px;
		display: inline-block;
		span {
			border-bottom: 1px dashed rgba($color-main2, 0.3);
			display: inline-block;
			margin-bottom: 10px;
		}
		textarea {
			border-radius: 3px;
			max-width: 412px;
			background-color: #ffffff;
			text-decoration: none;
			border: 1px solid rgba($color-main2, 0.3);
			padding: 8px 16px 8px;
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			transition: border-color .3s, box-shadow .3s;
			outline: none;
			&:focus {
				border-color: $color-blue5e;
				box-shadow: 0px 0px 0px 4px rgba($color-blue5e, 0.3);
			}
		}
	}
	&__text-right {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		left: 8px;
		bottom: -110px;
		font-family: $font-firaI;
		font-size: 14px;
		font-style: italic;
		line-height: 1.43;
		color: $color-red;
		&--active {
			opacity: 1;
			visibility: visible;
		}
	}
	&__wrap {
		display: none;
		&--active {
			display: block;
		}
		&-curier {
			padding-top: 20px;
		}
	}
	&__min {
		position: absolute;
		bottom: -27px;
		left: 0;
		margin-bottom: 0 !important;
		display: none;
		&--active {
			display: block;
		}
	}
	&__text-span {
		display: none;
		&--active {
			display: block;
		}
	}
	&__tabs-tk {
		.about__tab {
			border-radius: 3px;
			cursor: auto;
		}
	}
	&__delivery {
		display: none;
		&--active {
			display: flex;
		}
	}
}
.checkout__date {
	display: inline-block;
	position: relative;
	border-radius: 3px;
	box-shadow: 0 1px 3px 0 rgba(182, 188, 198, 0.65);
	background-image: linear-gradient(to bottom, #ffffff, #f2f2f3);
	border: 1px solid rgba($color-main2, 0.3);
	padding: 10px 33px 10px 16px;
	cursor: pointer;
	outline: none;
	min-width: 258px;
	margin-bottom: 31px;
	&:hover {
		outline: none;
	}
	&:focus {
		outline: none;
	}
	&::before {
		content: '';
		position: absolute;
		top: 17px;
		right: 12px;
		width: 10px;
		height: 5.6px;
		background: url("../images/sprites/svg/arrow.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
	}
}
.hidden {
	position: absolute;
	opacity: 0;
	border: none;
	width: 20px;
	height: 20px;
	color: transparent;
	left: 13px;
	top: -1px;
	outline: none;
	cursor: pointer;
}

.xdsoft_noselect {
	transform: translate(-14px, 33px);
}
.checkout__address {
	margin-bottom: 0;
	input {
		width: 410px;
		max-width: 410px;
	}
	.modal__min {
		margin-bottom: 0;
	}
}
// oplata
.checkout {
	.checkout__tabs-pay {
		margin-bottom: 28px;
		.about__tab {
			width: 241px;
			height: 100px;
			display: flex;
			flex-direction: column;
			align-items: start;
			padding-top: 16px;
			&--active {
				svg use {
					fill: $color-white;
				}
			}
		}
		.about__tab--one {
			border-radius: 3px;
		}
		span {
			margin-left: 0;
			margin-top: 9px;
		}
		svg {
			height: 16px;
		}
	}
	&__btn {
		display: none !important;
		&--active {
			display: flex !important;
		}
	}
}
.modal__map-link--curier {
	display: none;
}
.checkout__wrap-curier .modal__map-link--curier {
	margin-top: 18px;
	display: block;
}


// checkout date

.checkout {
	&__date {
		.link-modal {
			top: 48px;
			background-color: $color-white;
		}
	}
	&__mouth-wrap {
		padding: 15px 16px 12px;
		span {
			font-size: 14px;
			color: #7c7c82;
			margin-right: 25px;
		}
	}
	&__day-wrap {
		padding: 0 16px 15px;
	}
	&__day-box {
		display: flex;
		border-radius: 3px;
		background-color: #f7f7fa;
	}
	&__day {
		cursor: pointer;
		width: 73px;
		height: 36px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #f7f7fa;
		border-right: 1px solid #e7e7ee;
		color: $color-blue5;
		position: relative;
		z-index: 1;
		transition: background-color .3s;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: -1;
			background-color: transparent;
			-webkit-border-radius: 3px;
			-moz-border-radius: 3px;
			border-radius: 3px;
		}
		&--active::before {
			background-color: #393185;
		}
		&:first-child {
			-webkit-border-radius: 3px 0 0 3px;
			-moz-border-radius: 3px 0 0 3px;
			border-radius: 3px 0 0 3px;
		}
		&:last-child {
			-webkit-border-radius: 0 3px 3px 0;
			-moz-border-radius: 0 3px 3px 0;
			border-radius: 0 3px 3px 0;
			border-right: 1px solid transparent;
		}
		&--active {
			color: #fff;
			border-right: none;
		}
	}
	&__time {
		display: block;
		position: relative;
		border-top: 1px solid #dddde5;
		cursor: pointer;
		&:first-child {
			border-radius: 3px 3px 0 0;
		}
		&:hover {
			background: linear-gradient(to right, rgba(94, 77, 249, 0), rgba(57, 49, 133, 0.13));
		}
		&--active{
			background-color: #f4f4f4;
			&:hover {
				background: #f4f4f4;
			}
		}
		span {
			display: block;
			padding: 7px 9px 10px 20px;
			font-size: 16px;
			color: #292930;
		}
	}
	&__time--disabled {
		opacity: 0.6;
		pointer-events: none;
		cursor: default;
	}
	&__day--disabled {
		opacity: 0.6;
		pointer-events: none;
		cursor: default;
	}
	&__mouth--none {
		display: none;
	}
	.modal__min--mb {
		margin-bottom: 6px;
	}
}
.checkout {
	.checkout__tabs-tk {
		.modal__min {
			margin-bottom: 2px;
		}
		.about__tab {
			height: 50px;
			width: 166px;
		}
	}
	&__final {
		font-size: 14px;
		line-height: 1.43;
		color: #626260;
		display: none;
		svg {
			margin-right: 12px;
		}
		&--active {
			display: flex;
		}
	}
	.modal__maps {
		box-sizing: content-box;
	}
	.modal__maps-wrap {
		box-sizing: content-box;
		padding-right: 1px;
	}
	#modal__map {
		box-sizing: content-box;
		width: 234px;
		height: 236px
	}
}
