.account-wrap {
	.title-left {
		margin-top: 25px;
	}
}
.account {
	&-left {
		width: 790px;
		border-radius: 4px;
		background-color: $color-white;
		border: 1px solid $color-dark-bg;
		padding: 39px 32px 75px 32px;
	}
	&__wrap {
		border: solid 1px #e1e1e2;
		border-radius: 3px;
		padding: 39px 24px 0;
	}
	&__wrap:last-child {
		margin-top: 20px;
	}
	&__label {
		display: flex;
		position: relative;
		align-items: center;
		margin-bottom: 28px;
		span {
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			width: 150px;
		}
		input {
			border-radius: 3px;
			max-width: 180px;
			background-color: #ffffff;
			text-decoration: none;
			border: 1px solid rgba($color-main2, 0.3);
			padding: 8px 16px 8px;
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			transition: border-color .3s, box-shadow .3s;
			outline: none;
			&:focus {
				border-color: $color-blue5e;
				box-shadow: 0px 0px 0px 4px rgba($color-blue5e, 0.3);
			}
		}
	}
	&__title {
		font-family: $font-firaM;
		font-size: 22px;
		font-weight: 500;
		line-height: 1.27;
		color: $color-main2;
		margin-bottom: 33px;
	}
	&__bottom {
		padding: 23px 0 27px;
		position: relative;
		z-index: 0;
		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: -24px;
			width: calc(100% + 48px);
			height: 100%;
			z-index: -1;
			background-color: #f4f4f4;
			border-radius: 0 0 3px 3px;
		}
	}
	.btn-green {
		margin-right: 20px;
	}
	.modal__label {
		flex-direction: row;
		align-items: center;
		margin-bottom: 28px;
		&:first-child {
			margin-bottom: 28px;
		}
		span {
			font-family: $font-fira;
			font-size: 16px;
			line-height: 1.5;
			color: $color-main2;
			width: 150px;
			text-align: left;
		}
	}
	.modal__pas {
		left: 300px;
		top: 22px;
	}
}
.account__wrap--disabled {
	display: none;
}
.account__wrap--no-active {
	label {
		pointer-events: none;
	}
	input {
		background-color: #f4f4f4;
		pointer-events: none;
	}
}
.account__bottom--no-active {
	display: none;
}
.account__password {
	border: 1px solid rgba($color-main2, 0.3);
	border-radius: 3px;
	padding: 24px 24px 28px;
	background-color: #f4f4f4;
	margin-top: 20px;
}
.btn-green--account {
	width: 150px;
	padding-left: 0;
	padding-right: 0;
}

.account {
	&__item {
		display: flex;
		justify-content: space-between;
		padding: 20px 0 18px;
		border-top: 1px solid #e1e1e2;
	}
	&__order {
		width: 325px;
	}
	&__check {
		margin-top: 10px;
		display: flex;
		align-items: center;
		span {
			font-size: 14px;
			line-height: 1.43;
			color: rgba($color-main2, 0.8);
			margin-left: 8px;
		}
	}
	&__no-price {
		font-size: 16px;
		color: $color-red;
		text-decoration: line-through;
		text-align: right;
		margin-bottom: 5px;
	}
	&__price {
		text-align: right;
	}
	&__data {
		width: 170px;
		text-align: left;
	}
	.link-dashed-s {
		margin-left: 18px;
		font-size: 14px;
		line-height: 1.43;
	}
}
.account-history {
	.account__wrap:last-child {
		margin-top: 30px;
		padding-bottom: 26px;
	}
	.account__title {
		margin-bottom: 22px;
	}
}
.account {
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
	&__box {
		display: flex;
		align-items: flex-end;
	}
	&__prev {
		display: flex;
		align-items: center;
		.link-solid-black-s {
			font-size: 15px;
		}
		svg {
			width: 8px;
			height: 5px;
			transform: rotate(-270deg);
			margin-right: 7px;
			use {
				fill: $color-main2;
			}
		}
	}
}
.history-order {
	.account__title {
		margin: 0;
	}
	&__summ {
		width: 100%;
		text-align: right;
		font-family: $font-firaM;
		font-weight: 500;
		font-size: 19px;
		line-height: 1.26;
		position: relative;
		padding-top: 22px;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: -32px;
			width: calc(100% + 64px);
			height: 1px;
			border-bottom: 1px solid #e1e1e2;
		}
	}
	.account__check {
		padding-bottom: 24px;
	}
	.account__item {
		padding: 26px 0 27px;
	}
	.basket__img {
		width: 70px;
		height: 70px;
		border-color: #dddde5;
	}
	.basket__img img {
		max-height: 64px;
		max-width: 64px;
	}
	.basket__text {
		margin-left: 23px;
	}
	.basket__name {
		line-height: normal;
		margin-top: -3px;
		font-size: 17px;
		margin-bottom: 4px;
	}
	.basket__size {
		font-size: 15px;
	}
	.account__no-price {
		font-size: 19px;
		margin-top: -4px;
	}
	.account__price {
		font-size: 19px;
	}
}
.account-left--history-padding {
	padding-bottom: 51px;
}