.advantage {
	display: flex;
	justify-content: space-between;
	margin-top: 27px;
	&__left {
		width: 790px;
		border-radius: 4px;
		background-color: $color-white;
		border: 1px solid $color-dark-bg;
		padding: 64px 100px 75px 32px;
	}
	&__right {
		width: 250px;
		max-height: 234px;
		border-radius: 4px;
		background-color: $color-white;
		border: 1px solid $color-dark-bg;
		padding: 22px 20px 35px;
	}
	&__item {
		margin-bottom: 2px;
		a {
			font-size: 16px;
			line-height: 2.15;
			color: $color-blue5;
			cursor: default;
		}
		&--active {
			a {
				color: $color-main2;
				span {
					border-bottom-color: transparent;
				}
			}
			&:hover a{
				color: $color-main2;
				span {
					border-bottom-color: transparent;
				}
			}
		}
	}
	.title-left {
		margin-top: 0;
		margin-bottom: 23px;
		font-size: 34px;
	}
	.par {
		font-size: 18px;
		line-height: 1.56;
		margin: 13px 0;
	}
	&__bold {
		font-family: $font-firaM;
		font-size: 21px;
		font-weight: 500;
		line-height: 1.33;
		margin-bottom: 13px;
		color: $color-main2;
		margin-top: 28px;
	}
	.link-solid-black-s {
		font-size: 18px;
		line-height: 1.56;
		color: $color-main2;
		display: block;
	}
	&__txt {
		margin-top: 25px;
		font-size: 14px;
		line-height: 1.43;
		color: #5f5f67;
	}
	&__italic {
		font-family: $font-firaI;
		font-size: 16px;
		font-style: italic;
		line-height: 1.5;
	}
	&__par {
		margin-bottom: 8px;
	}
	&__delivery {
		.par {
			margin: 12.5px 0 15.5px;
		}
	}
	&__minibold {
		font-family: $font-firaM;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.56;
		color: $color-main2;
		margin-bottom: 2px;
	}
	&__guarantee {
		.par {
			margin: 0 0 16px;
		}
		.advantage__bold {
			margin-top: 32px;
		}
		.advantage__point {
			margin-bottom: 8px;
		}
	}
	&__discount {
		&-img {
			width: 339px;
			height: 224px;
			margin-left: -4px;
			margin-top: 29px;
			margin-bottom: 22px;
			img {
				max-width: 100%;
				max-height: 100%;
			}
		}
		.par {
			margin: 0 0 10px;
		}
		.advantage__italic {
			font-size: 18px;
			margin: 0 0 5px;
		}
		.advantage__bold {
			margin-top: 5px;
		}
	}
}
.advantage .par.discount-par {
	margin-bottom: 15px;
}