.checkout-ok {
	padding: 46px 32px 47px;
	background-color: $color-white;
	border: 1px solid rgba($color-dark-bg, 0.8);
	border-radius: 4px;
	max-width: 517px;
	margin: 89px auto 0;
	&__title {
		font-family: $font-firaM;
		font-size: 26px;
		font-weight: 500;
		text-align: center;
		color: $color-txt1;
		margin-bottom: 8px;
	}
	&__subtitle {
		font-size: 16px;
		line-height: 1.5;
		text-align: center;
		margin-bottom: 22px;
	}
	&__address {
		font-size: 16px;
		line-height: 1.5;
		margin-bottom: 25px;
		span {
			display: block;
		}
	}
	&__box {
		border-radius: 4px;
		border: 1px solid rgba($color-main2, 0.3);
		margin-bottom: 26px;
		&-top {
			font-size: 16px;
			line-height: 1.25;
			padding: 13px 18px 14px;
			border-bottom: 1px solid rgba($color-main2, 0.3);
		}
		&-main {
			font-size: 15px;
			line-height: 1.33;
			padding: 22px 18px 22px;
		}
		&-bottom {
			padding-top: 21px;
			border-top: 1px solid rgba($color-main2, 0.3);
		}
	}
	&__item {
		display: flex;
		justify-content: space-between;
		margin-bottom: 12px;
		font-size: 15px;
		line-height: 1.33;
	}
	&__item:last-child {
		padding-bottom: 9px;
	}
	&__sum {
		font-size: 15px;
		line-height: 1.33;
		margin-bottom: 13px;
		display: flex;
		justify-content: space-between;
	}
	&__sum:last-child {
		font-family: $font-firaM;
		font-size: 15px;
		font-weight: 500;
		line-height: 1.33;
		color: #292930;
	}
	&__detail {
		font-size: 14px;
		line-height: 1.43;
		color: #626260;
		margin-bottom: 21px;
		.link-dashed-blue-s {
			margin-bottom: 8px;
			display: inline-block;
		}
	}
	&__tel {
		font-size: 14px;
		line-height: 1.43;
		color: #626260;
	}
	.btn {
		display: inline-block;
		padding: 7px 0 9px;
		font-size: 15px;
		line-height: 1.6;
		width: 185px;
		height: 42px;
	}
	.card-product__mass-svg {
		width: 48px;
		height: 48px;
		.svg-check-2 {
			width: 26px;
			height: 29px;
		}
	}
}