.favorites {
	margin-top: 108px;
	margin-bottom: 88px;
	&__img {
		width: 240px;
		height: 240px;
		margin: 0 auto 28px;
		img {
			width: 100%;
			height: 100%;
		}
	}
	.title {
		font-size: 34px;
		margin-top: 20px;
		margin-bottom: 16px;
	}
	.desc {
		font-size: 22px;
		line-height: 1.45;
		color: $color-main2;
		margin-bottom: 24px;
	}
	.btn-reds {
		display: flex;
		margin: 0 auto;
		justify-content: center;
		align-items: center;
		width: 202px;
		height: 50px;
	}
}