.fav-con {
	padding-top: 8px;
	&__toolbar {
		display: flex;
		flex-wrap: wrap;
	}
	&__link {
		display: flex;
		margin-right: 26px;
		color: #94949a;
		letter-spacing: 0.1px;
		font-size: 17px;
		span {
			color: $color-blue5;
		}
		&--active {
			span {
				color: $color-main2;
				border-bottom-color: transparent;
			}
			&:hover span {
				border-bottom-color: transparent;
			}
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-top: 33px;
		margin-bottom: 26px;
		.product-slide__wrap {
			margin-bottom: 26px;
			min-height: 394px;
		}
		.product-slide__wrap:nth-child(4n+4) {
			margin-right: 0;
		}
	}
}
.product-slide__wrap--del:hover {
	.product-slide__item {
		box-shadow: none;
	}
	.product-slide__title {
		color: $color-main2;
	}
	.product-slide__bottom {
		display: none;
		max-height: 0;
		opacity: 0;
		visibility: hidden;
	}
	.product-slide__desc {
		max-height: 0;
		opacity: 0;
		visibility: hidden;
	}
	.product-slide__btn {
		background-color: $color-white;
		border: 1px solid rgba($color-main2, 0.3);
		box-shadow: none;
		color: $color-main2;
	}
}