.basket {
	border-radius: 4px;
	background-color: $color-white;
	border: 1px solid $color-dark-bg;
	&__form {
		margin-top: 46px;
	}
	&__top {
		display: flex;
		background-color: #eae5d2;
		align-items: center;
		padding: 11px 24px 13px;
	}
	&__txt {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
	}
	&__txt:first-child {
		width: 386px;
	}
	&__txt:nth-child(2) {
		width: 146px;
	}
	&__txt:nth-child(3) {
		width: 139px;
	}
	&__txt:nth-child(4) {
		width: 185px;
	}
	&__list {
		padding: 5px 23px 5px;
	}
	&__item {
		display: flex;
		padding: 30px 0;
		position: relative;
		border-top: 1px solid rgba($color-main2, 0.3);
		&--del {
			&::before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba($color-white, 0.6);
				z-index: 10;
			}
		}
	}
	&__item:first-child {
		border-top: none;
	}
	&__box:first-child {
		width: 386px;
		display: flex;
	}
	&__box:nth-child(2) {
		width: 146px;
	}
	&__box:nth-child(3) {
		width: 139px;
	}
	&__box:nth-child(4) {
		width: 185px;
	}
	&__img {
		width: 78px;
		height: 78px;
		border: 1px solid rgba($color-main2, 0.3);
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		img {
			max-width: 72px;
			max-height: 72px;
		}
	}
	&__text {
		margin-left: 19px;
	}
	&__name {
		display: block;
		line-height: 1.25;
		margin-bottom: 6px;
		margin-top: 2px;
	}
	&__art {
		font-size: 14px;
		color: rgba($color-gray, 0.8);
		margin-bottom: 6px;
	}
	&__size {
		font-family: $font-firaI;
		font-size: 14px;
		font-style: italic;
		line-height: 1.43;
	}
	&__price {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		&::after {
			content: ' руб.';
		}
	}
	&__noprice {
		font-size: 16px;
		line-height: 1.5;
		color: $color-red;
		text-decoration: line-through;
		&::after {
			content: ' руб.';
		}
	}
	&__all {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		&::after {
			content: ' руб.';
		}
	}
	&__col {
		display: flex;
		margin-top: -2px;
		&-input {
			width: 56px;
			height: 30px;
			border-left: none;
			border-right: none;
			border-top: 1px solid rgba($color-main2, 0.3);
			border-bottom: 1px solid rgba($color-main2, 0.3);
			text-align: center;
			pointer-events: none;
		}
		&-min {
			border-radius: 2px 0 0 2px;
			width: 30px;
			height: 30px;
			padding: 0;
			position: relative;
			&::before {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				width: 10px;
				height: 2px;
				background-color: $color-gray;
				transform: translate(-50%, -50%);
			}
		}
		&-max {
			border-radius: 0 2px 2px 0;
			width: 30px;
			height: 30px;
			padding: 0;
			position: relative;
			&::before {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				width: 10px;
				height: 2px;
				background-color: $color-gray;
				transform: translate(-50%, -50%);
			}
			&::after {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				width: 2px;
				height: 10px;
				background-color: $color-gray;
				transform: translate(-50%, -50%);
			}
		}
	}
	&__svg {
		position: absolute;
		right: 0;
		top: 16px;
		cursor: pointer;
		visibility: hidden;
		opacity: 0;
		z-index: 12;
		&--active {
			visibility: visible;
			opacity: 1;
		}
	}
	&__del {
		width: 16px;
		height: 20px;
		&:hover {
			svg use {
				fill: #9A9AB7;
			}
		}
	}
	&__back {
		width: 18px;
		height: 16px;
		&:hover {
			svg use {
				fill: #9A9AB7;
			}
		}
	}
	&__bottom {
		margin-top: 23px;
		display: flex;
		justify-content: space-between;
	}
	&__left {
		display: flex;
		padding-top: 12px;
	}
	&__address {
		display: flex;
		margin-left: 16px;
		font-family: $font-firaM;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.33;
		color: $color-main2;
		a {
			position: relative;
			.link-modal {
				left: -20px;
			}
			svg {
				margin-left: 1px;
				margin-right: 10px;
				width: 14px;
				height: 9.6px;
			}
			svg use {
				fill: $color-blue5;
			}
		}
	}
	&__address-price {
		&::before {
			content: '— ';
		}
	}
	&__right {
		border-radius: 4px;
		background-color: $color-white;
		border: 1px solid $color-dark-bg;
		width: 450px;
		padding: 27px 32px 20px;
		.btn-reds {
			width: 202px;
			height: 50px;
			margin-bottom: 9px;
			font-size: 17px;
			padding-bottom: 6px;
		}
	}
	&__buy {
		display: flex;
		margin-bottom: 10px;
		font-size: 16px;
		line-height: 1.25;
		color: $color-main2;
		&-left {
			width: 223px;
		}
		&-right {
			&::after {
				content: ' руб.';
			}
		}
	}
	&__buy:nth-child(3) {
		font-family: $font-firaM;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.11;
		margin-bottom: 25px;
		margin-top: 12px;
	}
	&__sale {
		&::before {
			content: '-';
		}
		&::after {
			content: '%';
		}
	}
	&__link {
		position: relative;
		svg {
			margin-right: 12px;
		}
		svg use {
			fill: $color-blue5;
			stroke: $color-blue5;
		}
	}
	&__link-modal {
		top: 29px;
		left: -21px;
		box-shadow: 0 2px 6px 0 rgba(35, 35, 42, 0.26);
	}
}