.svg {
	&-user {
		width: 10px;
		height: 12px;
		use {
			fill: $color-main2;
		}
	}
	&-logo {
		width: 168px;
		height: 40px;
		use {
			fill: $color-white;
		}
	}
	&-search {
		width: 20px;
		height: 20px;
		use {
			fill: $color-white;
		}
	}
	&-arrow {
		width: 12px;
		height: 7.6px;
		use {
			fill: $color-white;
			stroke: $color-white;
		}
	}
	&-like {
		width: 18px;
		height: 16px;
		use {
			fill: $color-white;
			transition: fill .3s;
		}
	}
	&-shop {
		width: 16px;
		height: 14px;
		display: none;
		&--active {
			display: block;
		}
		use {
			fill: $color-red;
		}
	}
	&-sale {
		width: 44px;
		height: 44px;
	}
	&-prime {
		width: 60px;
		height: 48px;
		use {
			fill: $color-blue5e;
		}
	}
	&-social {
		width: 32px;
		height: 32px;
	}
	&-mmit {
		height: 22.5px;
		width: 90px;
	}
	&-close {
		width: 10px;
		height: 10px;
		use {
			fill: #7D7D7D;
		}
	}
	&-arrow_6 {
		width: 15px;
		height: 12px;
		use {
			transition: fill .3s;
		}
	}
	&-filter {
		width: 10px;
		height: 6px;
		use {
			fill: $color-main2;
		}
	}
	&-next {
		width: 10px;
		height: 6px;
		transform: rotate(-90deg);
		use {
			fill: $color-blue5e;
		}
	}
	&-prev {
		width: 10px;
		height: 6px;
		transform: rotate(90deg);
		use {
			fill: $color-blue5e;
		}
	}
	&-pas_1 {
		width: 18px;
		height: 15px;
		use {
			fill: #808086;
		}
	}
	&-pas_2 {
		width: 18px;
		height: 15px;
		use {
			fill: #808086;
		}
	}
	&-breadcrumbs {
		width: 9px;
		height: 5px;
		transform: rotate(-90deg);
		margin-right: 8px;
		margin-left: 8px;
		use {
			fill: rgba(45, 43, 35, 0.77);
		}
	}
	&-arrow-left {
		width: 8px;
		height: 5px;
		transform: rotate(-270deg);
		use {
			fill: $color-main2;
		}
	}
	&-check-2 {
		width: 22px;
		height: 24px;
		use {
			fill: $color-white;
		}
	}
	&-pay {
		width: 62px;
		height: 12px;
	}
	&-check_s {
		width: 10px;
		height: 12px;
	}
	&-del {
		height: 20px;
		width: 16px;
		use {
			fill: #BABAD3;
			transition: fill .3s;
		}
	}
	&-back {
		width: 18px;
		height: 16px;
		use {
			fill: #BABAD3;
			transition: fill .3s;
		}
	}
	&-car {
		width: 34px;
		height: 22px;
	}
	&-acc-check {
		width: 11px;
		height: 12px;
		use {
			fill: $color-green;
		}
	}
	&-acc-close {
		width: 10px;
		height: 10px;
		use {
			fill: $color-red;
		}
	}
	&-acc-clock {
		width: 12.5px;
		height: 12.5px;
	}
	&-logo-checkout {
		width: 168px;
		height: 79px;
		use {
			fill: $color-red;
		}
	}
	&-shop_2 {
		width: 15.8px;
		height: 20px;
	}
	&-curier {
		width: 16px;
		height: 20px;
	}
	&-cdek {
		width: 23px;
		height: 16px;
	}
	&-money {
		width: 16px;
		height: 16px;
		use {
			fill: $color-blue5;
		}
	}
	&-filter-close {
		width: 6px;
		height: 6px;
		use {
			fill: #9393a0;
		}
	}
	&-ssl {
		width: 10px;
		height: 12px;
	}
}

