.stock {
	&__item {
		display: flex;
		padding: 31px 0 33px;
		border-top: 1px solid #e1e1e2;
	}
	&__item:first-child {
		padding-top: 8px;
		border-top: none;
	}
	&__item:last-child {
		padding-bottom: 0;
	}
	&__img {
		width: 192px;
		height: 134px;
		margin-right: 24px;
		border: 1px solid #e1e1e2;
		box-sizing: content-box;
		img {
			width: 100%;
			height: 100%;
		}
	}
	&__title {
		font-family: $font-firaM;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.56;
		color: $color-main2;
		margin-top: 0;
		margin-bottom: 2px;
	}
	&__data {
		font-family: $font-firaI;
		font-size: 15px;
		font-style: italic;
		color: rgba($color-main2, 0.8);
		margin-bottom: 15px;
	}
	&__desc {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
	}
	.link-solid-black-s {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
	}
}