.result {
	padding-top: 7px;
	.toolbar__check {
		margin-right: 0;
		line-height: normal;
		font-size: 16px;
		letter-spacing: -0.5px;
	}
	&__toolbar {
		display: flex;
		justify-content: space-between;
	}
	&__box {
		display: flex;
		flex-wrap: wrap;
	}
	&__link {
		display: flex;
		margin-right: 26px;
		color: #94949a;
		letter-spacing: 0.1px;
		font-size: 17px;
		span {
			color: $color-blue5;
		}
		&--active {
			span {
				color: $color-main2;
				border-bottom-color: transparent;
			}
			&:hover span {
				border-bottom-color: transparent;
			}
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-top: 35px;
		padding-bottom: 26px;
		.product-slide__wrap {
			margin-bottom: 26px;
			min-height: 394px;
		}
		.product-slide__wrap:nth-child(4n+4) {
			margin-right: 0;
		}
	}
}