.title {
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	color: $color-txt1;
	margin: 78px 0 36px;
}
.title--desc {
	margin-bottom: 11px;
}
.title-bread {
	margin: 55px 0 47px;
	display: block;
	font-family: $font-firaM;
	font-size: 34px;
	font-weight: 500;
	color: $color-txt1;
	letter-spacing: 0.4px;
}
.title-left {
	margin: 76px 0 36px;
	display: block;
	font-family: $font-firaM;
	font-size: 34px;
	font-weight: 500;
	color: $color-txt1;
	letter-spacing: 0.4px;
}
.subtitle {
	margin: 57px 0 36px;
	font-family: $font-firaM;
	font-size: 32px;
	font-weight: 500;
	color: $color-txt1;
}
.desc {
	font-family: $font-fira;
	font-size: 20px;
	line-height: 1.4;
	text-align: center;
	color: $color-txt1;
	margin: 0;
}
.btn {
	border-radius: 3px;
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	font-family: $font-fira;
	font-size: 15px;
	line-height: 1.6;
	text-align: center;
	color: $color-main2;
	padding: 3px 17px 5px;
	cursor: pointer;
	transition: background-color .3s, color .3s, box-shadow .3s, border-color .3s;
	text-decoration: none;
}
.btn-red {
	box-shadow: 0 1px 4px 0 rgba($color-red, 0.63);
	background-color: $color-red;
	border-color: $color-red;
	font-size: 16px;
	line-height: 1.5;
	color: $color-white;
	padding-top: 10px;
	padding-bottom: 14px;
	transition: background-color .3s, box-shadow .3s;
	&:hover {
		background-color: $color-light-red;
	}
	&:active {
		box-shadow: inset 0 1px 2px 0 $color-dark-red;
		background-color: $color-red;
	}
}
.btn-reds {
	border-radius: 3px;
	box-shadow: 0 1px 4px 0 rgba(230, 72, 64, 0.63);
	background-color: $color-red;
	padding: 3px 16px 5px;
	line-height: 1.6;
	color: $color-white;
	font-size: 15px;
	border: 1px solid $color-red;
	cursor: pointer;
	span {
		line-height: 1.6;
	}
	&:hover {
		background-color: $color-light-red;
	}
	&:active {
		box-shadow: inset 0 1px 2px 0 $color-dark-red;
		background-color: $color-red;
	}
}
.btn-disabled {
	opacity: 0.6;
}
.btn-dis {
	opacity: 0.6;
	cursor: pointer;
	pointer-events: none;
}
.btn-red.btn-disabled:hover {
	background-color: $color-red
}
.btn-grad {
	display: inline-block;
	cursor: pointer;
	padding: 3px 16px 5px;
	border-radius: 3px;
	background: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	transition: background .3s, box-shadow .3s, border-color .3s;
	font-size: 15px;
	line-height: 1.6;
	text-align: center;
	color: $color-main2;
	&:hover {
		border-color: rgba($color-main2, 0.4);
		box-shadow: 0 1px 3px 0 rgba(182, 188, 198, 0.65);
		background: linear-gradient(to bottom, #ffffff, #f2f2f3);
	}
	&:active {
		border-color: rgba($color-main2, 0.4);
		box-shadow: inset 0 1px 2px 0 rgba(133, 133, 143, 0.6);
		background: #f8f6f6;
	}
}
.btn-green {
	display: inline-block;
	cursor: pointer;
	padding: 3px 16px 5px;
	border-radius: 3px;
	background-color: #1fc350;
	border: 1px solid #19A643;
	transition: background .3s, box-shadow .3s, border-color .3s;
	font-size: 15px;
	line-height: 1.6;
	text-align: center;
	color: $color-white;
	box-shadow: 0 1px 4px 0 rgba(12, 109, 41, 0.45);
	&:hover {
		box-shadow: 0 1px 4px 0 rgba(12, 109, 41, 0.45);
		border: solid 1px #11b643;
		background-color: #18d351;
	}
	&:active {
		box-shadow: inset 0 1px 2px 0 #0b7f2e;
		border: solid 1px #19a643;
		background-color: #18b548;
	}
}
.link-solid-black {
	cursor: pointer;
	color: $color-main2;
	border-bottom: 1px solid rgba($color-main2, 0.3);
	transition: border-bottom-color .3s;
	&:hover{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-solid-blue {
	cursor: pointer;
	color: $color-blue5;
	border-bottom: 1px solid rgba($color-blue5e, 0.3);
	transition: border-bottom-color .3s;
	&:hover{
		border-bottom-color: rgba($color-blue5e, 0.6);
	}
}
.link-solid-blue-s {
	cursor: pointer;
	color: $color-blue5;
	& > span {
		border-bottom: 1px solid rgba($color-blue5e, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover > span{
		border-bottom-color: rgba($color-blue5e, 0.6);
	}
}
.link-solid-black-s {
	cursor: pointer;
	color: $color-main2;
	& > span {
		border-bottom: 1px solid rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover > span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-dashed-s {
	cursor: pointer;
	color: $color-main2;
	border: none;
	font-family: $font-fira;
	cursor: pointer;
	background-color: transparent;
	& > span {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover > span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.link-dashed-blue-s {
	cursor: pointer;
	border: none;
	font-family: $font-fira;
	cursor: pointer;
	background-color: transparent;
	color: $color-blue5;
	& > span {
		border-bottom: 1px dashed rgba($color-blue5, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover > span{
		border-bottom-color: rgba($color-blue5, 0.6);
	}
}
.link {
	cursor: pointer;
	display: inline-block;
	font-family: $font-fira;
	font-size: 14px;
	color: $color-main2;
	cursor: pointer;
	& > span {
		border-bottom: 1px dashed rgba($color-main2, 0.3);
		transition: border-bottom-color .3s;
	}
	&:hover > span{
		border-bottom-color: rgba($color-main2, 0.6);
	}
}
.box {
	margin-bottom: 23px;
}
.box:last-child {
	margin-bottom: 0;
}
.h3-title {
	font-family: $font-firaM;
	font-size: 14px;
	font-weight: 500;
	color: $color-main2;
	margin-bottom: 12px;
}
.par {
	max-width: 800px;
	font-family: $font-fira;
	font-size: 13px;
	line-height: 1.54;
	color: rgba($color-main2, 0.9);
}
.ul-items {

}
.ul-items__item {
	padding-left: 8px;
	font-size: 13px;
	line-height: 1.54;
	color: rgba($color-main2, 0.9);
	margin-top: 8px;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 1px;
		transform: translateY(-50%);
		width: 3px;
		height: 3px;
		background-color: rgba($color-main2, 0.9);
		border-radius: 50%;
	}
}
.to-favorites {
	position: absolute;
	top: 0;
	left: 0;
	width: 96px;
	text-align: center;
	padding: 4px 0;
	box-shadow: 0 1px 3px 0 rgba(61, 60, 72, 0.2);
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	border-radius: 3px;
	font-size: 13px;
	color: rgba($color-main2, 0.8);
	z-index: 1;
	visibility: hidden;
	opacity: 0;
	transition: opacity .3s, visibility .3s;
	&::after {
		content: '';
		position: absolute;
		width: 14px;
		height: 14px;
		bottom: -7px;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
		background-color: $color-white;
		border: 1px solid rgba($color-main2, 0.3);
		z-index: -2;
	}
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $color-white;
		border-radius: 6px;
		z-index: -1;
	}
	&--active {
		z-index: 15;
		opacity: 1;
		visibility: visible;
	}
}
.in-favorites {
	position: absolute;
	top: 0;
	left: 0;
	width: 96px;
	text-align: center;
	padding: 4px 0;
	box-shadow: 0 1px 3px 0 rgba(61, 60, 72, 0.2);
	background-color: $color-white;
	border: 1px solid rgba($color-main2, 0.3);
	border-radius: 6px;
	font-size: 13px;
	color: rgba($color-main2, 0.8);
	visibility: hidden;
	opacity: 0;
	transition: opacity .3s, visibility .3s;
	&::after {
		content: '';
		position: absolute;
		width: 14px;
		height: 14px;
		bottom: -7px;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
		background-color: $color-white;
		border: 1px solid rgba($color-main2, 0.3);
		z-index: -2;
	}
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $color-white;
		border-radius: 6px;
		z-index: -1;
	}
	&--active {
		z-index: 15;
		opacity: 1;
		visibility: visible;
	}
}
.overlay-dark {
	&::before {
		content: '';
		position: fixed;
		width: 100vw;
		height: 100vh;
		top: 0;
		left: 0;
		z-index: 20;
		background-color: rgba($color-main2, 0.3);
		transition: background-color .3s;
	}
}
.label {
	display: flex;
	position: relative;
	span {
		font-family: $font-fira;
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		min-width: 126px;
	}
	input {
		border-radius: 3px;
		max-width: 220px;
		background-color: #ffffff;
		text-decoration: none;
		border: 1px solid rgba($color-main2, 0.3);
		padding: 8px 16px 8px;
		font-family: $font-fira;
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		transition: border-color .3s, box-shadow .3s;
		outline: none;
		&:focus {
			border-color: $color-blue5e;
			box-shadow: 0px 0px 0px 4px rgba($color-blue5e, 0.3);
		}
	}
	textarea {
		border-radius: 3px;
		max-width: 360px;
		background-color: #ffffff;
		text-decoration: none;
		border: 1px solid rgba($color-main2, 0.3);
		padding: 8px 16px 8px;
		font-family: $font-fira;
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		transition: border-color .3s, box-shadow .3s;
		outline: none;
		resize: none;
		&:focus {
			border-color: $color-blue5e;
			box-shadow: 0px 0px 0px 4px rgba($color-blue5e, 0.3);
		}
	}
}
.italic {
	font-family: $font-firaI;
	font-size: 14px;
	font-style: italic;
	line-height: 1.29;
	color: #626260;
}
.link-modal {
	transition: max-height .6s;
	z-index: 2;
	width: 100%;
	position: absolute;
	top: 30px;
	left: 0;
	min-width: 254px;
	overflow: hidden;
	max-height: 0;
	&--active {
		max-height: 1000px;
	}
	&__wrap {
		border-radius: 3px;
		box-shadow: 0 2px 6px -2px rgba(35, 35, 42, 0.26);
		background-color: #ffffff;
		border: 1px solid rgba($color-main2, 0.3);
	}
	&__item:first-child {
		border-radius: 3px 3px 0 0;
		overflow: hidden;
	}
	&__item:last-child {
		border-radius: 0 0 3px 3px;
		overflow: hidden;
		.link-modal__option {
			border-bottom: none;
		}
	}
	&__option {
		position: relative;
		display: block;
		border-bottom: 1px solid #dddde5;
		cursor: pointer;
		&:hover {
			background: linear-gradient(to right, rgba(94, 77, 249, 0), rgba(57, 49, 133, 0.13));
		}
		span {
			display: block;
			padding: 7px 9px 10px 20px;
			font-size: 16px;
			color: #292930;
		}
	}
	&__hidden {
		position: absolute;
		opacity: 0;
		z-index: -1;
	}
	&__item {
		&:last-child label {
			border-bottom: none;
		}
		&--active {
			.link-modal__option {
				background-color: #f4f4f4;
				&:hover {
					background: #f4f4f4;
				}
			}
		}
	}
}
.br-mobile {
	display: none;
}

.color-check {
	margin-bottom: 13px;
	input {
		position: absolute;
		z-index: -1;
		opacity: 0;
		margin: 10px 0 0 20px;
	}
	&__text {
		position: relative;
		padding: 0 0 0 29px;
		cursor: pointer;
		color: $color-main2;
		font-size: 15px;
		line-height: 1.33;
		&::after {
			content: '';
			position: absolute;
			top: 4px;
			left: 3px;
			width: 12px;
			height: 10px;
			background: url(../images/sprites/svg/check.svg) center no-repeat;
			-webkit-background-size: cover;
			-moz-background-size: cover;
			-o-background-size: cover;
			background-size: cover;
			opacity: 0;
			-webkit-transition: opacity .3s;
			-o-transition: opacity .3s;
			-moz-transition: opacity .3s;
			transition: opacity .3s;
		}
	}
	&__beffor {
		position: absolute;
		top: 0;
		left: 0;
		width: 18px;
		height: 18px;
		border-radius: 2px;
		border: 1px solid rgba(41, 41, 48, 0.3);
		background: $color-white;
		transition: background-color .3s, border-color .3s;
	}
}
.color-check input:checked + .color-check__text::after {
	opacity: 1;
}
.color-check input:checked + .color-check__text .color-check__beffor {
	border-color: $color-blue5e;
	background: $color-blue5e;
}