.address {
	&__list {
		display: flex;
		flex-wrap: wrap;
	}
	&__item {
		width: 33%;
		margin-bottom: 15px;
		padding: 15px 0 24px;
		border: 1px solid transparent;
		position: relative;
		z-index: 0;
		&--active {
			&::before {
				content: '';
				position: absolute;
				z-index: -1;
				width: 270px;
				height: 100%;
				top: 0;
				left: -20px;
				border-radius: 3px;
				background-color: #f1edde;
				border-color: $color-dark-bg;
			}
			.address__name {
				color: $color-main2;
				span {
					border-bottom-color: transparent;
				}
				&:hover span{
					border-bottom-color: transparent;
				}
			}
		}
	}
	&__name {
		display: block;
		font-size: 20px;
		line-height: 1.4;
		margin-bottom: 10px;
	}
	&__time {
		font-size: 16px;
		margin-bottom: 5px;
	}
	&__tel {
		font-family: $font-firaM;
		font-weight: 500;
	}
	&__subtitle {
		font-family: $font-firaM;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.33;
		color: $color-main2;
		margin-top: 34px;
		margin-bottom: 14px;
	}
	&__slide {
		margin-right: 10px;
		max-height: 96px;
		display: inline-block;
		&-img {
			width: 124px;
			height: 84px;
			border: 1px solid rgba($color-main2, 0.3);
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				max-width: 100%;
				max-height: 100%;
			}
		}
	}
	.card-product__arrow {
		background: $color-bg;
		top: 32px;
	}
	.card-product__arrow--next {
		right: -40px;
	}
	&__pop {
		position: absolute;
		display: none;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 870px;
		height: 588px;
		&--active {
			display: block;
		}
	}
	&__slide-pop {
		width: 100%;
		height: 100%;
	}
	&__slide-pop-img {
		width: 870px;
		height: 588px;
		border: 12px solid $color-white;
		border-radius: 8px;
		display: flex;
		justify-content: center;
		align-items: center;
		img {
			width: 100%;
			max-height: 100%;
		}
	}
	&__arrow {
		position: absolute;
		top: 50%;
		width: 64px;
		height: 20px;
		z-index: 4;
		display: flex;
		align-items: center;
		cursor: pointer;
		transform: translateY(-50%);
		transition: opacity .3s, visibility .3s;
		svg {
			width: 8.5px;
			height: 37.9px;
			opacity: 0.9;
			transition: opacity .3s, fill .3s;
			use {
				fill: $color-white;
				box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.28);
			}
		}
		&--prev {
			left: 16px;
			justify-content: center;
			transform: translateY(-50%) rotate(-180deg);
		}
		&--next {
			right: 16px;
			justify-content: center;
		}
		&:hover {
			svg {
				opacity: 1;
				fill: $color-blue5;
			}
		}
	}
}
.mfp-bg.magnific-address {
	background-color: rgba($color-main2, 0.8);
}
.address__arrow.slick-disabled {
	opacity: 0;
	visibility: hidden;
}
.address__slider-pop {
	height: 0;
	opacity: 0;
	visibility: hidden;
	width: 870px;
	margin: 0 auto;
}
.mfp-wrap .address__slider-pop{
	opacity: 1;
	visibility: visible;
	height: 588px;
}