.about {
	border-radius: 4px;
	background-color: $color-white;
	border: 1px solid $color-dark-bg;
	margin-top: 27px;
	padding: 64px 50px 69px;
	overflow: hidden;
	.title {
		font-size: 34px;
		margin-top: 0;
		margin-bottom: 10px;
	}
	&__tabs {
		display: flex;
		justify-content: center;
		margin-top: 48px;
	}
	&__tab:first-child {
		border-radius: 3px 0 0 3px;
		border-right: none;
	}
	&__tab:last-child {
		border-radius: 0 3px 3px 0;
		border-left: none;
	}
	&__tab {
		display: flex;
		width: 141px;
		height: 42px;
		box-shadow: 0 1px 3px 0 rgba(182, 188, 198, 0.65);
		background: linear-gradient(to bottom, #ffffff, #f2f2f3);
		border: 1px solid rgba($color-main2, 0.3);
		justify-content: center;
		align-items: center;
		font-size: 15px;
		color: $color-main2;
		padding-bottom: 3px;
		&:hover {
			background: linear-gradient(to bottom, #ffffff, #e6e5ef);
		}
		&:active {
			box-shadow: inset 0 1px 3px 0 #161145;
			background: #393185;
			color: $color-white;
		}
		&--active {
			box-shadow: inset 0 1px 3px 0 #161145;
			background: #393185;
			color: $color-white;
			&:hover {
				box-shadow: inset 0 1px 3px 0 #161145;
				background: #393185;
				color: $color-white;
				cursor: default;
			}
		}
	}
	&__content {
		margin-top: 23px;
	}
	&__company {
		max-width: 660px;
		margin: 0 auto;
	}
	&__img {
		margin-top: 19px;
		margin-bottom: 26px;
		border: 1px solid rgba($color-main2, 0.3);
	}
	&__img img {
		width: 100%;
	}
	&__desc {
		font-size: 16px;
		line-height: 1.5;
		text-align: center;
	}
	&__par {
		font-size: 16px;
		line-height: 1.5;
		margin: 8px 0;
	}
	&__bold {
		font-family: $font-firaM;
		font-size: 21px;
		font-weight: 500;
		line-height: 1.33;
		color: #292930;
		margin-top: 33px;
		margin-bottom: 12px;
	}
	&__req {
		font-size: 16px;
		line-height: 2;
	}
	&__box {
		display: none;
		&--active {
			display: block;
		}
	}
	&__office {
		padding: 0 40px;
	}
	&__slider {
		margin-top: 43px;
		height: 253px;
		.card-product__arrow {
			background-color: $color-white;
			width: 270px;
			top: 110px;
			&--next {
				right: -142px;
			}
			&--prev {
				left: -142px;
			}
		}
	}
	&__item {
		width: 163px;
		margin-right: 19px;
		display: inline-block;
	}
	&__item-img {
		width: 163px;
		height: 206px;
		border: 1px solid rgba($color-main2, 0.3);
		img {
			width: 100%;
			height: 100%;
		}
	}
	&__name {
		text-align: center;
		margin-top: 16px;
	}
	&__info {
		text-align: center;
		margin-top: 36px;
		font-size: 18px;
		line-height: 1.56;
		.link-solid-blue {
			color: $color-blue5;
		}
	}
}